body {  margin: 0;  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",    sans-serif;  -webkit-font-smoothing: antialiased;  -moz-osx-font-smoothing: grayscale;}code {  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",    monospace;}
.App {
  font-size: 18px; }

.main-section {
  display: grid;
  grid-template-columns: 1fr 3fr; }

.nav-top {
  position: relative; }
  .nav-top:before {
    content: '';
    bottom: 0;
    left: 0;
    position: absolute;
    width: 100%;
    height: 2px;
    background-color: #ff6d00;
    -webkit-transition: 0.2s;
    transition: 0.2s;
    -webkit-transform: scaleX(0);
            transform: scaleX(0); }
  .nav-top:hover:before {
    -webkit-transform: scaleX(1);
            transform: scaleX(1); }

header {
  display: grid;
  grid-auto-flow: column;
  justify-content: space-between;
  padding: 10px;
  background-color: #37474f;
  color: #fff; }

.header-item {
  padding: 10px 20px;
  font-size: 20px;
  text-transform: capitalize;
  cursor: pointer; }

.header-section {
  display: grid;
  align-items: center;
  grid-auto-flow: column; }

.header-logo {
  font-weight: bold; }

.selected {
  color: #ff6d00; }
  .selected:before {
    content: '';
    bottom: 0;
    left: 0;
    position: absolute;
    width: 100%;
    height: 2px;
    background-color: #ff6d00; }

.header-button {
  border: 3px solid #ff6d00;
  color: #ff6d00;
  border-radius: 4px;
  background: transparent;
  cursor: pointer; }
  .header-button:hover {
    background: #ff6d00; }

a {
  text-decoration: none;
  color: #fff; }

.header-button {
  font-weight: 800; }
  .header-button:hover {
    color: #37474f; }

input {
  margin-right: 20px; }


.category-section {
  display: grid;
  grid-template-columns: 1fr 3fr;
  height: 85vh; }

.left-section {
  grid-column: 1 / 2;
  background-color: #757575; }

.right-cell a {
  position: relative; }
  .right-cell a:before {
    content: '';
    bottom: 0;
    left: 0;
    position: absolute;
    width: 100%;
    height: 2px;
    background-color: #ff6d00;
    -webkit-transition: 0.2s;
    transition: 0.2s;
    -webkit-transform: scaleX(0);
            transform: scaleX(0); }
  .right-cell a:hover:before {
    -webkit-transform: scaleX(1);
            transform: scaleX(1); }

.right-section {
  grid-column: 2 / 3;
  background-color: #bdbdbd; }

.data-container {
  width: 100%; }

.row {
  display: grid;
  grid-template-columns: 1fr 3fr; }

.left-cell {
  grid-column: 1 / 2;
  padding-left: 10px; }

.right-cell {
  grid-column: 2 / 3; }
  .right-cell a {
    color: #ff6d00; }

.left-section {
  grid-column: 1 / 2;
  background-color: #757575; }

.legend {
  text-align: center;
  color: #ff6d00; }

.left-section-item {
  padding: 10px;
  margin: 0;
  position: relative; }
  .left-section-item:before {
    content: '';
    bottom: 0;
    left: 0;
    position: absolute;
    width: 3px;
    height: 0%;
    background-color: #ff6d00;
    -webkit-transition: 0.3s;
    transition: 0.3s; }
  .left-section-item:hover:before {
    height: 100%; }

h5 {
  padding: 0 10px;
  margin: 0; }

.selected-left {
  color: #ff6d00; }
  .selected-left:before {
    content: '';
    bottom: 0;
    left: 0;
    position: absolute;
    width: 3px;
    height: 100%;
    background-color: #ff6d00; }

.pagination ul, .page-buttons-container {
  display: grid;
  grid-auto-flow: column;
  grid-gap: 10px;
  justify-content: center; }

.pagination {
  margin-top: 20px;
  display: flex;
  flex-direction: column;
  justify-content: center; }
  .pagination ul {
    list-style: none;
    margin: 0;
    padding: 0; }

.page-buttons-container {
  border: 3px solid #ff6d00;
  grid-template-columns: repeat(4, 39px);
  padding: 10px;
  margin: 10px 50px; }

.page-button {
  border: 3px solid #ff6d00;
  color: #ff6d00;
  border-radius: 4px;
  background: transparent;
  cursor: pointer; }
  .page-button:hover {
    background: #ff6d00; }

.page-button {
  width: 39px;
  height: 39px; }
  .page-button a {
    color: #ff6d00; }
  .page-button:hover {
    color: #757575; }
    .page-button:hover a {
      color: #757575; }

.modal {
  position: fixed;
  height: 100%;
  width: 100%;
  top: 0;
  left: 0;
  display: flex;
  align-items: center;
  justify-content: center; }

#modal-root {
  position: relative;
  z-index: 999; }

.modal {
  background-color: #37474f; }

.loading {
  position: relative;
  width: 70px;
  height: 20px;
  display: flex;
  flex-direction: row;
  justify-content: space-between; }

.dot {
  display: inline-block;
  width: 20px;
  height: 20px;
  border-radius: 50%;
  background: skyblue;
  -webkit-animation-name: scale;
          animation-name: scale;
  -webkit-animation-duration: 1s;
          animation-duration: 1s;
  -webkit-animation-iteration-count: infinite;
          animation-iteration-count: infinite;
  -webkit-animation-timing-function: linear;
          animation-timing-function: linear; }

@-webkit-keyframes scale {
  0% {
    -webkit-transform: scale(1);
            transform: scale(1); }
  50% {
    -webkit-transform: scale(0.6);
            transform: scale(0.6); }
  100% {
    -webkit-transform: scale(1);
            transform: scale(1); } }

@keyframes scale {
  0% {
    -webkit-transform: scale(1);
            transform: scale(1); }
  50% {
    -webkit-transform: scale(0.6);
            transform: scale(0.6); }
  100% {
    -webkit-transform: scale(1);
            transform: scale(1); } }

.d1 {
  -webkit-animation-delay: 0s;
          animation-delay: 0s; }

.d2 {
  -webkit-animation-delay: 0.33s;
          animation-delay: 0.33s; }

.d3 {
  -webkit-animation-delay: 0.66s;
          animation-delay: 0.66s; }

.error-modal {
  position: fixed;
  height: 100%;
  width: 100%;
  top: 0;
  left: 0;
  display: flex;
  align-items: center;
  justify-content: center; }

.error-page {
  background-color: #37474f;
  color: #37474f;
  width: 100%;
  height: 100%; }

